
body {
    overflow-y: hidden; 
    animation: addScroll 2s forwards;
}


img {
	position: absolute;
	top: 35%;
	left: 50%;
	transform: translate(-50%,-50%);
}

.container{
	height: 20px;
	width: 140px;
	border: 1px solid white;
	border-radius: 4px;
	position: absolute;
	left: 50%;
    top: 40%;
	overflow: hidden;	
	transform: translate(-50%,0%);
}

.Loader{
	height: 16px;
	width: 10px;
	float: left;
	background: white;
	margin-top: 2px;
	margin-left: 5px;
	border-radius: 2px;
	box-shadow: inset 0 0 12px blue;
	position: relative;
	left: -50px;
	animation: move1 2s linear infinite;
}

.backdrop {
    background-color: black;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 9999999;
    opacity: 1;
    animation: fadeaway 2s forwards;
}

@keyframes addScroll {
    80% {
        overflow-y: hidden; 
    }
    100% {
        overflow-y: auto; 
    }
}

@keyframes fadeaway {
    0%{
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    99% {
        height: auto;
        width: auto;
    }
    100% {
        opacity: 0;
        height: 0;
        width: 0;
    }
}

@keyframes move1 {
	97%{
		left: 150px;
		opacity: 1;
	}
	98%{
		opacity: 0;
		left: 150px;
	}
	99%{
		left: -50px;
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}