body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
    overflow-y: hidden; 
    -webkit-animation: addScroll 2s forwards; 
            animation: addScroll 2s forwards;
}


img {
	position: absolute;
	top: 35%;
	left: 50%;
	transform: translate(-50%,-50%);
}

.container{
	height: 20px;
	width: 140px;
	border: 1px solid white;
	border-radius: 4px;
	position: absolute;
	left: 50%;
    top: 40%;
	overflow: hidden;	
	transform: translate(-50%,0%);
}

.Loader{
	height: 16px;
	width: 10px;
	float: left;
	background: white;
	margin-top: 2px;
	margin-left: 5px;
	border-radius: 2px;
	box-shadow: inset 0 0 12px blue;
	position: relative;
	left: -50px;
	-webkit-animation: move1 2s linear infinite;
	        animation: move1 2s linear infinite;
}

.backdrop {
    background-color: black;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 9999999;
    opacity: 1;
    -webkit-animation: fadeaway 2s forwards;
            animation: fadeaway 2s forwards;
}

@-webkit-keyframes addScroll {
    80% {
        overflow-y: hidden; 
    }
    100% {
        overflow-y: auto; 
    }
}

@keyframes addScroll {
    80% {
        overflow-y: hidden; 
    }
    100% {
        overflow-y: auto; 
    }
}

@-webkit-keyframes fadeaway {
    0%{
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    99% {
        height: auto;
        width: auto;
    }
    100% {
        opacity: 0;
        height: 0;
        width: 0;
    }
}

@keyframes fadeaway {
    0%{
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    99% {
        height: auto;
        width: auto;
    }
    100% {
        opacity: 0;
        height: 0;
        width: 0;
    }
}

@-webkit-keyframes move1 {
	97%{
		left: 150px;
		opacity: 1;
	}
	98%{
		opacity: 0;
		left: 150px;
	}
	99%{
		left: -50px;
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}

@keyframes move1 {
	97%{
		left: 150px;
		opacity: 1;
	}
	98%{
		opacity: 0;
		left: 150px;
	}
	99%{
		left: -50px;
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}
/*========== General ===============*/
body {
  box-sizing: border-box;
  font-family: Muli, sans-serif;
  text-decoration: none;
  padding: 0;
  margin: 0; }

a {
  color: black;
  text-decoration: none;
  cursor: pointer; }

h1.heading {
  width: 100%;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
  letter-spacing: 0.03em;
  font-size: 33px;
  transform: translateY(30px);
  opacity: 0;
  -webkit-animation: 1s ease 0.2s forwards slideIn;
          animation: 1s ease 0.2s forwards slideIn; }

p.paragraph {
  color: dimgray;
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 185%;
  letter-spacing: 0.03em;
  margin: 50px 0px;
  -webkit-animation: 2s ease 0.2s forwards slideIn;
          animation: 2s ease 0.2s forwards slideIn; }

/*========== Containers ===============*/
.container {
  height: auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 100px 0px 200px;
  padding: 0 40px; }

.sub-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0px; }

.skill-content {
  width: 100%; }

.text-content {
  max-width: 700px; }

/*========== Skillz ===============*/
.skill-table {
  width: 100%;
  flex-wrap: wrap;
  text-align: left;
  display: flex;
  justify-content: space-between;
  -webkit-animation: 3s ease 0.2s forwards slideIn;
          animation: 3s ease 0.2s forwards slideIn; }

.skill-table p.paragraph {
  color: black;
  font-weight: 700;
  min-width: 200px;
  margin: 20px 0px 0px; }

.skill-table p span {
  font-size: 20px;
  margin-right: 5px;
  color: #b4e1e7; }

/*========== Projects ===============*/
.project-container {
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 0px; }

.project-row {
  width: 100%;
  height: auto;
  margin-bottom: 90px;
  transition-duration: 0.3s; }

.project-row:nth-child(1) {
  margin-right: 10%; }

.project-image {
  width: 100%;
  height: 300px;
  transition-duration: 0.3s;
  border-radius: 4px;
  background-size: cover;
  background-position-x: 50%;
  background-position-y: 50%; }

.project-image-1 {
  background-image: url("https://cdn.dribbble.com/users/1044993/screenshots/11918320/media/9f195afcf730070520ac572dbcd21be5.png"); }

.project-image-2 {
  background-image: url("https://cdn.dribbble.com/users/1044993/screenshots/11699091/media/610d8d5bb9f45b1faccf4feda60c5929.png"); }

.project-link {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 160%;
  display: inline;
  color: black;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin: 50px 0px;
  border-bottom: 2px solid transparent;
  transition: border-bottom 0.3s ease 0s; }

.project-link:hover {
  border-bottom: 2px solid black; }

/*========== Mobile ===============*/
@media (min-width: 600px) {
  h1.heading {
    font-size: 40px;
    transform: scale(1); }
  p.paragraph {
    /* max-width: 70%; */ }
  .skill-table {
    flex-wrap: nowrap; }
  .skill-table p.paragraph {
    min-width: unset;
    margin: 30px 0px; }
  .project-row {
    width: 45%;
    margin-bottom: 0px; } }

/*========== Animations ===============*/
@-webkit-keyframes slideIn {
  from {
    transform: translateY(30px);
    opacity: 0; }
  to {
    transform: translateY(0);
    opacity: 1; } }
@keyframes slideIn {
  from {
    transform: translateY(30px);
    opacity: 0; }
  to {
    transform: translateY(0);
    opacity: 1; } }

